.app {
  height: 100vh;
  background-color: #181e29;
  text-align: center;
  color: white;
}

.button-container {
  position: fixed;
  width: 100vw;
  background-color: white;
  bottom: 0;
  padding: 10px;
}

.button-container .button {
  margin: 0 10px;
}

.floating-struct {
  display: none;
  width: max-content;
  position: fixed;
  opacity: 60%;
}